import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-study-completed',
  templateUrl: './study-completed.page.html',
  styleUrls: ['./study-completed.page.scss'],
})
export class StudyCompletedPage implements OnInit {
  audioData:any;
  bookData:any;
  constructor(public route:ActivatedRoute, public location:Location) { }

  ngOnInit() {
    this.bookData = JSON.parse(this.route.snapshot.paramMap.get('bookData'));
    this.audioData =  JSON.parse(this.route.snapshot.paramMap.get('audioData'));
    console.log(this.audioData);
  }

  goback(){
    this.location.back();
  }

}
